import React from 'react'
import { fetchUserData } from '../store'

const TestRemove = ({ data }) => {
    return (
        <div>
            {data}
        </div>
    )
}

TestRemove.getInitialProps = ({ reduxStore }) => {
    let res = reduxStore.dispatch(fetchUserData())
    return {
        data: res
    }
}

export default TestRemove